import React, { useEffect, useState } from "react"
import Layout from "../../containers/layout"
import CardListing from "../../components/ItemList/index.js"
import {
  Anchor,
  Box,
  Grommet,
  List,
  Menu,
  Card,
  CardBody,
  CardFooter,
  Heading,
  Image,
} from "grommet"
import { graphql } from "gatsby"
import { More } from "grommet-icons"

var shuffle = function (array) {
  var currentIndex = array.length
  var temporaryValue, randomIndex

  // While there remain elements to shuffle...
  while (0 !== currentIndex) {
    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex)
    currentIndex -= 1

    // And swap it with the current element.
    temporaryValue = array[currentIndex]
    array[currentIndex] = array[randomIndex]
    array[randomIndex] = temporaryValue
  }

  return array
}

const Index = ({ data }) => {
  const [dataList, setDataList] = useState(
    data.allYoutubeVideo.edges.map(e => ({
      ...e.node,
      thumbnail: e.node.thumbnail.url,
      url: `http://www.youtube.com/watch?v=${e.node.videoId}`,
      cta: (
        <Anchor
          href={`http://www.youtube.com/watch?v=${e.node.videoId}`}
          label="Watch"
        />
      ),
    }))
  )
  return (
    <Layout max="1240px">
      <CardListing dataList={dataList} />
    </Layout>
  )
}

export default Index

export const query = graphql`
  query HomePage1 {
    allYoutubeVideo {
      edges {
        node {
          id
          title
          description
          videoId
          publishedAt
          privacyStatus
          channelTitle
          thumbnail {
            url
          }
        }
      }
    }
  }
`
