import React from "react"
import {
  Image,
  Anchor,
  Box,
  Heading,
  Card,
  CardBody,
  CardFooter,
  List,
  Menu,
} from "grommet"
import { More } from "grommet-icons"
const Home = ({ dataList }) => {
  console.log(dataList)
  return (
    <List
      data={dataList}
      action={(item, index) => (
        <Menu
          key={index}
          icon={<More />}
          hoverIndicator
          items={[{ label: "Watch", href: item.url }]}
        />
      )}
      step={3}
      show={{ page: 7 }}
      paginate
    >
      {datum => (
        <Box direction="vertical" gap="small">
          <Image fit="cover" width="200px" src={datum.thumbnail} />
          {datum.title}
          {JSON.stringify()}
        </Box>
      )}
    </List>
  )
}

export default Home
